.NavigationItem { 
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a{ 
    color: rgb(44, 54, 143);
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 1000px;

}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active { 
    color: #fc7978;
}

@media (min-width: 500px) { 
    .NavigationItem { 
        margin: 0;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }

    .NavigationItem a{ 
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }

    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active { 
        color: #fc7978;
    }
}